@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
nav {
    display: flex;
    align-items: center;
    padding: 5px 50px 5px 15px;
    background-color: #ff8000;
    justify-content: space-between;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
}

.logo {
    height: 75px;
}

.cart {
    display: flex;
    font-size: 20px;
    align-items: center;
    color: rgba(0, 0, 0, 0.6);
    text-decoration: none;
}

.cart:hover {
    cursor: pointer;
    color: rgba(0, 0, 0, 0.7);
}

@media (max-width: 600px) {
    .home {
        width: 100%;
    }
    nav {
        width: 100%;
    }
    .cart-text {
        display: none;
    }
    svg {
        margin-right: 30px;
    }
}
.img-size {
    height: 150px;
}

.card {
    margin: 15px;
    width: 300px;
    padding: 10px;
    height: 300px;
    display: flex;
    text-align: center;
    border-radius: 3px;
    align-items: center;
    flex-direction: column;
    background-color: #dadada;
    transition: transform 0.2s;
    background-image: linear-gradient(#f3f3f3, #e0e0e0);
}

.card:hover {
    transform: scale(1.1);
}

.product {
    display: flex;
    flex: 1 0 21%;
    justify-content: center;
}

.product-text {
    margin: 15px;
}

.text-link {
    display: flex;
    font-size: 20px;
    align-items: center;
    text-decoration: none;
    justify-content: center;
    color: rgba(0, 0, 0, 0.6);
}

.text-link:hover {
    cursor: pointer;
    color: #ff8000;
}

@media (max-width: 600px) {
    .card {
        width: calc(100% - 50px);
        margin: 15px 10px 15px 7px;
    }
}
.home {
    margin: 30px;
    border-radius: 5px;
    background-color: #f7f7f7;
    box-shadow: 0 0px 7px rgba(0, 0, 0, 0.3);
}

.search-container {
    display: flex;
    margin-left: 30px;
    margin-top: -15px;
    color: #ff8000;
}

.search-container .input-width {
    width: calc(100% - 90px);
}

.products-container {
    display: flex;
    flex-wrap: wrap;
}

.imgs {
    display: flex;
    align-items: center;
    flex-direction: column;
    border-radius: 5px;
    background-color: #dadada;
    background-image: linear-gradient(#ffffff, #cecece);
}

.imgs .imgs-ninja {
    height: 400px;
    -webkit-animation: 0.75s ease-out 0s 1 slideInFromRight;
            animation: 0.75s ease-out 0s 1 slideInFromRight;
}

.imgs .imgs-logo {
    height: 300px;
    margin-top: -125px;
    -webkit-animation: 0.75s ease-out 0s 1 slideInFromLeft;
            animation: 0.75s ease-out 0s 1 slideInFromLeft;
}

@-webkit-keyframes slideInFromLeft {
    0% {
        transform: translateX(-100%);
    }
    100% {
        transform: translateX(0);
    }
}

@keyframes slideInFromLeft {
    0% {
        transform: translateX(-100%);
    }
    100% {
        transform: translateX(0);
    }
}

@-webkit-keyframes slideInFromRight {
    0% {
        transform: translateX(100%);
    }
    100% {
        transform: translateX(0);
    }
}

@keyframes slideInFromRight {
    0% {
        transform: translateX(100%);
    }
    100% {
        transform: translateX(0);
    }
}

@media (max-width: 600px) {
    .imgs .imgs-ninja {
        height: 200px;
    }
    .imgs .imgs-logo {
        height: 100px;
        margin-top: -50px;
    }
}
.card-cart {
    margin: 30px;
    display: flex;
    border-radius: 5px;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    background-color: #f7f7f7;
    padding: 30px 45px 15px 45px;
    box-shadow: 0 0px 7px rgba(0, 0, 0, 0.3);
}

.text-cart {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
}

svg {
    margin-right: 15px;
}

.amount-input {
    margin-top: 15px;
}

label svg:hover {
    transition: 0.2s;
    cursor: pointer;
    color: #3e3e3e;
}

.product-item {
    width: 100%;
    display: flex;
    border-radius: 3px;
    align-items: center;
    margin: 0 10px 15px 10px;
    border: 1px solid #c3c3c3;
    background-color: #f5f5f5;
    padding: 15px 20px 15px 20px;
    justify-content: space-between;
}

.product-img {
    width: 270px;
    height: 150px;
    margin-right: 30px;
    border: 1px solid #e6e6e6;
}

.container-img {
    display: flex;
}

.container-fields {
    display: flex;
    flex-direction: column;
}

.product-name {
    display: flex;
    font-size: 22px;
    margin-top: 30px;
    color: #181818;
    align-items: center;
    justify-content: space-between;
}
.details {
    margin: 30px;
    padding: 30px;
    border-radius: 5px;
    background-color: #f7f7f7;
    box-shadow: 0 0px 7px rgba(0, 0, 0, 0.3);
}

.container-details {
    display: flex;
}

.container-img-details {
    padding: 5px;
    margin-right: 15px;
    border-radius: 3px;
    flex-direction: column;
    border: 1px solid #cecece;
}

.name-product {
    padding: 10px;
    color: #ffffff;
    border-radius: 3px;
    margin-bottom: 15px;
    background-color: #ff8000;
}

.img-height {
    height: 350px;
}

.container-description {
    display: flex;
    padding: 30px;
    border-radius: 3px;
    flex-direction: column;
    border: 1px solid #cecece;
}

.price {
    font-size: 30px;
    text-align: right;
    margin: 30px 30px 30px 30px;
}

.container-price {
    display: flex;
    margin-bottom: 45px;
    align-items: center;
    flex-direction: column;
}
html, body {
    margin: 0;
    padding: 0;
    background-color: #0272cc;
    font-family: 'Roboto', sans-serif;
}

input {
    height: 40px;
    color: #030303;
    font-size: 16px;
    border-radius: 3px;
    padding: 0 10px 0 10px;
    border: 2px solid #ffa850;
    background-color: #ffffff;
}

button {
    height: 40px;
    width: 150px;
    transition: 0.2s;
    font-weight: 700;
    border-radius: 3px;
    border: 1px solid #c6c6c6;
    color: rgba(0, 0, 0, 0.7);
    background-color: #ff8000;
}

button:hover {
    cursor: pointer;
    background-color: #e87500;
}
